import {orderInvoiceList} from "@/api/userInfo";
import moment from '@/utils/moment';
import rfLoadMore from '@/components/rf-load-more/rf-load-more';

export default {
	components: {
		rfLoadMore
	},
	data() {
		return {
			timeOutEvent: 0,
			source: 0,
			page: 1,
			loadingType: 'more',
			invoiceList: [],
			loading: true,
		}
	},
	filters: {
		time(val) {
			return moment(val * 1000).format('YYYY-MM-DD HH:mm')
		}
	},
	onLoad(option){
		this.source = option.source;
		this.initData()
	},
	//下拉刷新
	onPullDownRefresh(){
		this.page = 1;
		this.invoiceList = [];
		this.getInvoiceList('refresh');
	},
	//加载更多
	onReachBottom(){
		this.page ++;
		this.getInvoiceList();
	},
	methods: {
		/**
		 *@des 初始化数据
		 *@author stav stavyan@qq.com
		 *@blog https://stavtop.club
		 *@date 2019/11/18 09:57:30
		 */
		initData () {
			this.getInvoiceList();
		},
		/**
		 *@des 获取收货地址列表
		 *@author stav stavyan@qq.com
		 *@blog https://stavtop.club
		 *@date 2019/11/18 09:58:15
		 */
		async getInvoiceList (type) {
			await this.$get(`${orderInvoiceList}`,{
				page: this.page
			}).then(r=>{
		    this.loading = false;
				if (type === 'refresh') {
					uni.stopPullDownRefresh();
				}
				this.loadingType = r.data.length === 10 ? 'more' : 'nomore';
				this.invoiceList = [...this.invoiceList, ...r.data];
			}).catch(() => {
		    this.loading = false;
				if (type === 'refresh') {
					uni.stopPullDownRefresh();
				}
			})
		},
		navTo(id){
			uni.navigateTo({
				url: `/pages/order/detail?id=${id}`
			})
		}
	}
}
